@media (max-width: 666px) {
  .container {
    max-width: 144rem; } }

.banner {
  padding: 10rem 0 4.4rem 0;
  position: relative; }
  .banner-inner {
    position: relative; }
  .banner-steps {
    position: absolute;
    left: -15rem;
    top: 15%; }
    .banner-steps > * {
      width: 100%;
      height: 100%; }
  .banner-dashed {
    width: 31rem;
    height: 40rem;
    position: absolute;
    right: 0;
    bottom: 0; }
    .banner-dashed > * {
      object-fit: contain;
      width: 100%;
      height: 100%; }
  .banner-top__text {
    display: flex;
    align-items: center;
    margin-bottom: 2.4rem; }
    .banner-top__text-line {
      margin-right: 1rem; }
    .banner-top__text-subtitle {
      color: #01A3A4;
      font-size: 1.4rem;
      line-height: 1.6rem;
      letter-spacing: 0.25em;
      text-transform: uppercase; }
  .banner-bottom__title > h3 {
    display: inline-block;
    font-size: 9.6rem;
    line-height: 9.6rem;
    color: #000;
    width: 100%; }
  .banner-bottom__subtitle > span {
    text-transform: inherit;
    font-size: 7.2rem;
    line-height: 11.7rem;
    justify-content: flex-end;
    display: flex;
    background: linear-gradient(141.39deg, #74E6CF 13.88%, #C0E594 106.32%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-fill-color: transparent; }
  .banner-bottom__img {
